var r = function r() {};

r.parseGrid = function (r) {
  var n = new RegExp("[^01\\n]", "g");
  return r.replace(n, "").split("\n").filter(function (r) {
    return r.length > 0;
  }).map(function (r) {
    return r.split("").map(function (r) {
      if ("0" === r) return 0;
      if ("1" === r) return 1;
      throw new Error("Logic error. This should never happen!");
    });
  });
};

export { r as SeaParser };